@import '../../App.css';

header button {
    display: flex;
    align-self: center;
    justify-content: center;
    align-items: center;
    padding: 5% 13%;
    margin-top: 5%;

    font-family: 'Josefin Sans', sans-serif;
    font-weight: bold;
    color: #fcf7f7;

    background-color: #942571;
    border: 2px solid #942571;
    border-radius: 5px;
}

header button:hover,
header button:focus-within,
header button:focus {
    background-color: transparent;
}

/* IPAD STYLES */

@media (min-width: 768px) {
    header button {
        margin-top: 0;
        margin-left: 5%;
        padding: 2% 7%;
    }
}

/* SMALL SCREEN STYLES */
@media (min-width: 992px) {
    header button {
        font-size: 1.3rem;
    }
}

/* LARGE SCREENS STYLES */
@media (min-width: 1200px) {
    header button {
        font-size: 1.5rem;
    }
}